// =====================================================
//      Filter
// =====================================================

.filter {
    position: relative;
    color: $filter-color;
    z-index: 0;

    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: $filter-background-color;
        z-index: -1;
    }
}
